    --body-1: 400 12px / 150% 'Montserrat';
    --body-2: 400 12px / 150% 'Montserrat';
    --body-3: 400 12px / 150% 'Montserrat';
    --body-4: 400 12px / 150% 'Montserrat';

    /*---Fonts---*/
